.wlr-loader {
    display: inline-block;
    position: relative;
    width: 64px;
    height: 64px;
    left: 40%;
}
.wlr-loader div {
    position: absolute;
    border: 4px solid #FF5722;
    opacity: 1;
    border-radius: 50%;
    animation: wlr-loader 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.wlr-loader div:nth-child(2) {
    animation-delay: -0.5s;
}
@keyframes wlr-loader {
0% {
        top: 28px;
        left: 28px;
        width: 0;
        height: 0;
        opacity: 1;
}
100% {
        top: -1px;
        left: -1px;
        width: 58px;
        height: 58px;
        opacity: 0;
}
}

.wlr-modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  min-height: 360px;
  background: #000;
  opacity: .7;
  z-index: 99900;
}
.wlr-modal * {
  box-sizing: border-box;
}
.wlr-modal .wlr-modal-content {
  position: fixed;
  background: #fff;
  z-index: 100000;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  width: 500px;
}
.wlr-modal .wlr-modal-main.has-footer {
  padding-bottom: 55px;
}
.wlr-modal header.modal-header {
  height: auto;
  background: #fcfcfc;
  padding: 1em 1.5em;
  border-bottom: 1px solid #ddd;
}
.wlr-modal header.modal-header h1 {
  margin: 0;
  padding: 0;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.5em;
}
.wlr-modal header.modal-header .modal-close-link {
  cursor: pointer;
  color: #777;
  height: 54px;
  width: 54px;
  padding: 0;
  position: absolute;
  top: 0;
  right: 0;
  text-align: center;
  border: 0;
  border-left: 1px solid #ddd;
  background-color: transparent;
  -webkit-transition: color 0.1s ease-in-out, background 0.1s ease-in-out;
  transition: color 0.1s ease-in-out, background 0.1s ease-in-out;
}
.wlr-modal header.modal-header .modal-close-link::before {
  font: normal 22px/50px dashicons !important;
  color: #666;
  display: block;
  content: '\F335';
  font-weight: 300;
}
.wlr-modal header.modal-header .modal-close-link:hover {
  background: #ddd;
  border-color: #ccc;
  color: #000;
}
.wlr-modal .modal-body {
  min-height: 100px;
  padding: 15px;
  max-height: 150px;
  overflow-y: scroll;
  font-weight: 500;
}
.wlr-modal footer {
  position: absolute;
  left: 0;
  right: 0;
  bottom: -30px;
  z-index: 100;
  padding: 1em 1.5em;
  background: #fcfcfc;
  border-top: 1px solid #dfdfdf;
  -webkit-box-shadow: 0 -4px 4px -4px rgba(0, 0, 0, 0.1);
  box-shadow: 0 -4px 4px -4px rgba(0, 0, 0, 0.1);
}
.wlr-modal footer .inner {
  text-align: right;
  line-height: 23px;
}
@media only screen and (max-width: 500px) {
.wlr-modal-content {
    width: 400px !important;
    top: 300px !important;
}
}
@media only screen and (max-width: 376px) {
.wlr-modal-content {
